main {
    width: 700px;
    padding: 2rem 4rem;
    background-color: var(--white);
    border-radius: 30px;
    box-shadow: -20px 20px var(--black);
}

.quote-container {
    height: 50px;
}

#text {
    width: 100%;
    text-align: center;
    line-height: 30px;
}

#author {
    margin: 8rem 0 2rem 0;
    font-weight: bold;
}

.buttons {
    display: flex;
    justify-content: space-between;
}

#tweet-quote {
    text-decoration: none;
    color: inherit;
    cursor: pointer;
}

#new-quote, .social-network {
    display: block;
    width: 30%;
    padding: 0.8rem 1rem;
    font-family: inherit;
    font-size: 1.3rem;
    background-color: var(--normal-blue);
    border: 3px solid transparent;
    color: white;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.2s ease-out,
                color 0.2s ease-out,
                border 0.2s ease-out;
}

#new-quote:hover {
    color: black;
    background-color: transparent;
    border: 3px solid var(--normal-blue);
}

.social-network {
    background-color: var(--twitter-blue);
}

.social-network:hover {
    color: black;
    background-color: transparent;
    border: 3px solid var(--twitter-blue);
}

@media (max-width: 700px) {
    main {
        max-width: 400px;
    }

    html {
        font-size: 12px;
    }
    
    #new-quote, .social-network {
        width: 40%;
    }
}