* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    cursor: default;
}

:root {
    --white: hsl(0, 0%, 90%);
    --black: hsl(0, 0%, 15%);
    --twitter-blue: hsl(203, 89%, 53%);
    --normal-blue: hsl(240, 58%, 49%);
}

body {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    font-family: "Fira Code";
    font-size: 1.2rem;
    color: var(--black);
    background-color: white;
}

#root {
    display: flex;
    justify-content: center;
    align-items: center;
}